<template>

    <img v-if="loading" src="../assets/loading.gif" alt="Loading" class="loading" />

    <div v-else>


        <ol>
            <li>
                <div class="descr1">{{ totalMatchs }} games played to date.</div>
            </li>
            <li>
                <div class="descr2">{{ totalUsers }} players have passed through here.</div>
            </li>
        </ol>
    </div>

</template>

<script lang="js">
    import axios from 'axios';

    export default {
        created() {
            this.fetchTotalUsers();
        },
        data() {
            return {
                loading: false
            };
        },
        methods: {
            fetchTotalUsers() {
                this.loading = true;
                axios.get('https://planing-poker-api-lively-forest-7338.fly.dev/users/GetTotalNumberUsers')
                    .then(response => {
                        this.totalUsers = response.data;
                        this.fetchTotalGame();
                    }).catch(error => console.log(error))
            },
            fetchTotalGame() {
                axios.get('https://planing-poker-api-lively-forest-7338.fly.dev/game/GetNumberMatchs')
                    .then(response => {
                        this.totalMatchs = response.data;
                        setTimeout(() => {
                            this.loading = false;
                        }, 1000);
                    }).catch(error => console.log(error))
            },
        }
    }
</script>

<style scoped>
    @import "../css/info.css";
</style>