<template>
    <div class="cardlist">

        <div class="playingCards fourColours rotateHand">
            <ul class="table">
                <li v-for="card in firstcards" v-bind:key="card.value" @click="getCard($event)" v-bind:id="card.id">
                    <div class="card rango clubs">
                        <div class="rank"><img class="iconCard" src="../assets/logo-transparent.png" /></div>
                        <div class="suit">
                            <div v-if="card.value == 'coffee'" :class="getCardClass(card)">☕︎</div>
                            <div v-else :class="getCardClass(card)">{{ card.value }}</div>
                        </div>
                    </div>
                </li>
                <li v-for="card in secondcards" v-bind:key="card.value" @click="getCard($event)" v-bind:id="card.id">
                    <div class="card rango clubs">
                        <div class="rank"><img class="iconCard" src="../assets/logo-transparent.png" /></div>
                        <div class="suit">
                            <div v-if="card.value == 'coffee'" :class="getCardClass(card)">☕︎</div>
                            <div v-else :class="getCardClass(card)">{{ card.value }}</div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script lang="js">
    import axios from 'axios';
    import { getCurrentInstance } from "vue";

    export default {
        props: {
            id: String
        },
        setup() {
            const instance = getCurrentInstance();
            window.signalR = instance.appContext.config.globalProperties.$signalr;
        },
        data() {
            return {
                firstcards: this.firstcards,
                secondcards: this.secondcards
            };
        },
        created() {
            this.fetchData();
        },
        methods: {
            fetchData() {
                axios.get('https://planing-poker-api-lively-forest-7338.fly.dev/cards')
                    .then(response => {
                        this.firstcards = response.data.slice(0, 7);
                        this.secondcards = response.data.slice(7, 13);
                    }).catch(error => console.log(error))
            },
            insertVote(vote) {
                const votes = {
                    name: sessionStorage.getItem('username'),
                    group: sessionStorage.getItem('gameid'),
                    value: vote
                }
                axios.post('https://planing-poker-api-lively-forest-7338.fly.dev/votes', votes)
                    .then(function () {
                        window.signalR
                            .invoke('NotifyUserHasVoted', votes.group, votes.name, votes.value)
                            .catch(function (err) { console.error(err) })
                    }).catch(error => console.log(error))
            },
            getCard(event) {
                if (event) {
                    event.preventDefault()
                }

                const allElements = document.querySelectorAll('*');
                allElements.forEach((element) => {
                    element.classList.remove('selected');
                });

                let element = document.getElementById(event.currentTarget.id).firstElementChild;
                element.classList.add("selected");

                this.insertVote(event.currentTarget.textContent);
            },
            getCardClass(card) {
                switch (card.value) {
                    case "?":
                        return "interrogant";
                    case "coffee":
                        return "coffee";
                    case "0":
                        return "cero";
                    case "0,5":
                        return "cerocinco"
                    default:
                        return "cardNumber";
                }
            }
        },
    }
</script>

<style scoped>
    @import "../css/cardlist.css";
</style>